	import myOrderform from './businessComponents/myOrderForm'
	import {
		userorderNoexchange,
		usergiftrecordExchange
	} from 'api/api';
	let pageData = {
		totalPrice: '',
		count: '',
		currencyUnit: '',

		itemIcon: '',
		itemName: '',
	}
  export default{
        data () {
            return {
            	pageData: pageData,
            	tableData: '',
//          	orderData:[{
//		          state: '交易成功',
//		          name: '毛绒玩具',
//		          price: '5',
//		          num:'1',
//		          total:'5'
//		        }],
            	searchValue:'',
            	
                  currentPage: 1,
                  pageSizes: [10, 20, 30],
                  page: {
					start: 0,
					length: 10,
					draw: 1,
					},
                  dialogVisible:false,
                  dialogTyle:''
            }
        },
        components: {
          myOrderform
  		},
  		created() {
			this.init()
		},
		watch: {
			searchValue(value) {
				if(!value) {
					this.init()
				}
			}
		},
  		methods:{
  			showDilog(type,data){
               
                this.pageData={...data,
						...data.busiItem}
               
                  this.dialogVisible=true
                  this.dialogTyle=type
            },
            init() {
				//busItemName
				userorderNoexchange(this.page).then(res => {
					this.tableData = { ...res.data.obj,
						...res.data.obj.busiItem
					}
				})
			},
			onQuery() {
				if(this.searchValue) {
					userorderNoexchange({ ...this.page,
						busItemName: this.searchValue
					}).then(res => {
						this.tableData = { ...res.data.obj,
							...res.data.obj.busiItem
						}
					})
				}
			},
			getpageData() {
				userorderNoexchange({ ...this.page,
					busItemName: this.searchValue
				}).then(res => {
					this.tableData = { ...res.data.obj,
						...res.data.obj.busiItem
					}
				})
			},
			handleSizeChange(val) {
				this.page.length = val
				this.page.start = (this.currentPage - 1) * this.page.length
				this.getpageData()
			},
			handleCurrentChange(val) {
				this.currentPage = val;
				this.page.start = (this.currentPage - 1) * this.page.length
				setTimeout(() => {
					this.getpageData()
				}, 0)
			},
  		}
  }
	 



